import React, { useState } from "react";
import { Button, Header, Icon, Rating, Modal } from "semantic-ui-react";
import "./Modal.css";

function ModalExampleCloseIcon(props) {
  const [review, setReview] = useState({
    name: "",
    email: "",
    review: "",
    rating: 0,
  });
  const [group, setGroup] = useState({
    technology_Name: "",
    technology_link: "",
    about: "",
  });

  const [profile, setProfile] = useState({
    name: "",
    bio: "",
    Website: "",
    Github: "",
    Twitter: "",
    Instagram: "",
    Facebook: "",
    phoneNumber: "",
    mobileNumber: "",
    Address: "",
  });
  const [open, setOpen] = useState(false);

  const handleChangeInput = (e, type) => {
    e.preventDefault();
    if (type === "reviews") {
      const { name, value } = e.target;
      setReview((prevReview) => {
        return {
          ...prevReview,
          [name]: value,
        };
      });
    } else if (type === "profile") {
      const { name, value } = e.target;
      setProfile((prevProfile) => {
        return {
          ...prevProfile,
          [name]: value,
        };
      });
    } else if (type === "groups") {
      const { name, value } = e.target;
      setGroup((prevGroup) => {
        return {
          ...prevGroup,
          [name]: value,
        };
      });
    } else {
    }
  };
  const handleRate = (e, { rating }) => {
    // console.log(rating);
    let r = {};
    r["name"] = review.name;
    r["email"] = review.email;
    r["review"] = review.review;
    r["rating"] = rating;
    setReview(r);
  };
  const addingReviewLayout = () => {
    return (
      <div className="addingReview">
        Name
        <input
          type="text"
          name="name"
          placeholder="Name"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.name}
        />
        Email
        <input
          type="text"
          name="email"
          placeholder="Email"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.email}
        />
        Review
        <textarea
          type="text"
          name="review"
          placeholder="Type your review here"
          onChange={(e) => {
            handleChangeInput(e, "reviews");
          }}
          value={review.review}
        />
        <div className="addingReview__rating">
          <p>Ratings</p>
          <Rating
            maxRating={5}
            defaultRating={0}
            icon="heart"
            size="large"
            name="ratings"
            onRate={handleRate}
          />
        </div>
      </div>
    );
  };
  const addingTechnologyLayout = () => {
    // console.log(group);
    return (
      <div className="addingReview">
        Technology Name
        <input
          type="text"
          name="technology_Name"
          placeholder="Enter technology name"
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          value={group.technology_Name}
        />
        Technology Link
        <input
          type="text"
          name="technology_link"
          placeholder="Enter the link"
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          value={group.technology_link}
        />
        Type
        <select
          onChange={(e) => {
            handleChangeInput(e, "groups");
          }}
          name="about"
          value={group.about}
        >
          <option>None</option>
          {console.log(props.options) &&
            props.options.map((option) => {
              console.log(option);
              return <option>{option.value}</option>;
            })}
        </select>
      </div>
    );
  };
  const addingEditProfile = () => {
    return (
      <div className="addingReview">
        Name
        <input
          type="text"
          name="name"
          placeholder="Enter your name"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.Name}
        />
        Bio
        <input
          type="text"
          name="bio"
          placeholder="Enter your bio"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.bio}
        />
        Website Link
        <input
          type="text"
          name="Website"
          placeholder="Enter website link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.website}
        />
        Github Link
        <input
          type="text"
          name="Github"
          placeholder="Enter github link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.github}
        />
        Twitter Link
        <input
          type="text"
          name="Twitter"
          placeholder="Enter twitter link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.twitter}
        />
        Instagram Link
        <input
          type="text"
          name="Instagram"
          placeholder="Enter instagram link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.instagram}
        />
        Facebook Link
        <input
          type="text"
          name="Facebook"
          placeholder="Enter facebook link"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.facebook}
        />
        Phone Number
        <input
          type="text"
          name="phoneNumber"
          placeholder="Enter phone number"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.phone}
        />
        Mobile Number
        <input
          type="text"
          name="mobileNumber"
          placeholder="Enter mobile number"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.mobile}
        />
        Address
        <input
          type="text"
          name="Address"
          placeholder="Enter your address"
          onChange={(e) => {
            handleChangeInput(e, "profile");
          }}
          value={profile.address}
        />
      </div>
    );
  };
  return (
    <Modal
      closeIcon
      dimmer={"blurring"}
      style={{
        height: "max-content",
        left: "auto",
        right: "auto",
      }}
      open={open}
      trigger={props.trigger}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
    >
      <Header
        icon="add"
        content={
          props.type === "reviews"
            ? "Add your review"
            : props.type === "editProfile"
            ? "Edit Profile"
            : "Add your community"
        }
      />
      <Modal.Content scrolling>
        {props.type === "reviews"
          ? addingReviewLayout()
          : props.type === "editProfile"
          ? addingEditProfile()
          : addingTechnologyLayout()}
      </Modal.Content>
      <Modal.Actions>
        <Button color="red" onClick={() => setOpen(false)}>
          <Icon name="remove" /> Decline
        </Button>
        <Button
          color="green"
          onClick={() => {
            props.type === "reviews"
              ? props.revertingFCN(review)
              : props.type === "editProfile"
              ? props.revertingFCN(profile)
              : props.revertingFCN(group);
            setOpen(false);
          }}
        >
          <Icon name="checkmark" /> Submit
        </Button>
      </Modal.Actions>
    </Modal>
  );
}

export default ModalExampleCloseIcon;
