import React from "react";
import { Button, Modal } from "semantic-ui-react";

function ModalExampleTopAligned(props) {
  const [open, setOpen] = React.useState(false);

  return (
    <Modal
      centered={false}
      style={{
        height: "max-content",
        left: "auto",
        right: "auto",
      }}
      open={open}
      size={"tiny"}
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      trigger={props.trigger}
    >
      <Modal.Header>{props.details.technology_Name}</Modal.Header>
      <Modal.Content style={{ paddingLeft: "70px", paddingRight: "100px" }}>
        <span
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <span style={{ fontWeight: "900", fontSize: "1.1rem" }}>Topic</span>
          <Modal.Description style={{ wordWrap: "break-word" }}>
            {props.details.about}
          </Modal.Description>
        </span>
        <span
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
          }}
        >
          <span style={{ fontWeight: "900", fontSize: "1.1rem" }}>Link </span>
          <Modal.Description style={{ wordWrap: "break-word" }}>
            {props.details.technology_link}
          </Modal.Description>
        </span>
      </Modal.Content>
      <Modal.Actions>
        <Button onClick={() => setOpen(false)}>OK</Button>
      </Modal.Actions>
    </Modal>
  );
}

export default ModalExampleTopAligned;
