import React, { useEffect, useState } from "react";
import { Icon } from "semantic-ui-react";
import { Link as LinkScroll } from "react-scroll";
import { Link } from "react-router-dom";
import axios from "axios";

const Navbar = (props) => {
  const [open, setOpen] = useState(false);
  const [openProfileOrRegister, setOpenProfileOrRegister] = useState(false);
  const [loggedIn, setLoggedIn] = useState({
    isLoggedIn: false,
    userDetails: {},
  });
  useEffect(() => {
    if (props.id !== "") {
      axios
        .get(`${process.env.REACT_APP_BACKEND_URL}/${props.id}`)
        .then((res) => {
          if (res.data.validity) {
            setLoggedIn({ isLoggedIn: true, userDetails: res.data.message });
          }
        });
    }
  }, []);
  return (
    <div className="navbar">
      <LinkScroll to="homePage" smooth={true} duration={1000}>
        <div className="navbar__heading">
          <p> Find Community</p>
        </div>
      </LinkScroll>

      <div className="navbar__right">
        <LinkScroll to="aboutPage" smooth={true} duration={1000}>
          <div className="navbar__right__p">About</div>
        </LinkScroll>

        <LinkScroll to="communityPage" smooth={true} duration={1000}>
          <div className="navbar__right__p">Community</div>
        </LinkScroll>

        <LinkScroll to="reviewPage" smooth={true} duration={1000}>
          <div className="navbar__right__p">Reviews</div>
        </LinkScroll>

        <LinkScroll to="contactPage" smooth={true} duration={1000}>
          <div className="navbar__right__p">Contact</div>
        </LinkScroll>

        {loggedIn.isLoggedIn ? (
          <div className="navbar__right__p">
            <p
              style={{
                fontSize: "1.2rem",
                fontWeight: "900",
                marginBottom: "0%",
              }}
              onClick={() => setOpenProfileOrRegister(!openProfileOrRegister)}
            >
              Manjot
              {openProfileOrRegister ? (
                <Icon name="angle up" size="small" color="red" />
              ) : (
                <Icon name="angle down" size="small" color="red" />
              )}
            </p>
            <div
              className=""
              style={{
                width: "100px",
                height: "100px",
                background: "white",
                position: "absolute",
                borderRadius: "20%",
                border: "1px solid #aa0000",
                marginTop: "10px",
                display: openProfileOrRegister ? "" : "none",
                zIndex: "10000",
              }}
            >
              <ul
                style={{
                  listStyle: "none",
                  paddingLeft: "0px",
                  margin: "0px",
                  padding: "5px",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexDirection: "column",
                  zIndex: "10000",
                }}
              >
                <Link to="/userProfile">
                  <li style={{ paddingTop: "10px", paddingBottom: "10px" }}>
                    Profile
                  </li>
                </Link>

                <li
                  style={{ paddingTop: "10px", paddingBottom: "10px" }}
                  onClick={() => {
                    setLoggedIn({
                      isLoggedIn: false,
                      userDetails: {},
                    });
                    setOpenProfileOrRegister(!openProfileOrRegister);
                  }}
                >
                  Logout
                </li>
              </ul>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
      <div className="navbar__right__smallScreen">
        <div
          className="navbar__right__smallScreen__icons"
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {loggedIn.isLoggedIn ? (
            <div className="navbar__right__p">
              <p
                style={{
                  fontSize: "1.2rem",
                  fontWeight: "900",
                  marginBottom: "0%",
                }}
                onClick={() => setOpenProfileOrRegister(!openProfileOrRegister)}
              >
                {loggedIn.userDetails.name}
                {openProfileOrRegister ? (
                  <Icon name="angle up" size="small" color="red" />
                ) : (
                  <Icon name="angle down" size="small" color="red" />
                )}
              </p>
              <div
                className=""
                style={{
                  width: "100px",
                  height: "100px",
                  background: "white",
                  position: "absolute",
                  borderRadius: "20%",
                  border: "1px solid #aa0000",
                  marginTop: "10px",
                  display: openProfileOrRegister ? "" : "none",
                }}
              >
                <ul
                  style={{
                    listStyle: "none",
                    paddingLeft: "0px",
                    margin: "0px",
                    padding: "5px",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                  }}
                >
                  <Link to="/userProfile">
                    <li style={{ paddingTop: "10px", paddingBottom: "10px" }}>
                      Profile
                    </li>
                  </Link>
                  <li
                    style={{ paddingTop: "10px", paddingBottom: "10px" }}
                    onClick={() => {
                      setLoggedIn({
                        isLoggedIn: false,
                        userDetails: {},
                      });
                      setOpenProfileOrRegister(!openProfileOrRegister);
                    }}
                  >
                    Logout
                  </li>
                </ul>
              </div>
            </div>
          ) : (
            ""
          )}
          <div
            style={{ display: !open ? "" : "none", cursor: "pointer" }}
            onClick={() => setOpen(true)}
          >
            <Icon name="bars" size="big" color="red" />
          </div>
          <div
            style={{ display: open ? "" : "none", cursor: "pointer" }}
            onClick={() => setOpen(false)}
          >
            <Icon name="x" size="big" color="red" />
          </div>
        </div>
        <div
          className="navbar__right__smallScreen__content"
          style={{ display: open ? "block" : "none" }}
        >
          <ul style={{ listStyle: "none" }}>
            <LinkScroll to="aboutPage">
              <li style={{ borderRadius: "30px 30px 0px 0px" }}>About</li>
            </LinkScroll>

            <LinkScroll to="communityPage">
              <li>Community</li>
            </LinkScroll>

            <LinkScroll to="reviewPage">
              <li>Reviews</li>
            </LinkScroll>

            <LinkScroll to="contactPage">
              <li style={{ borderRadius: "0px 0px 30px 30px" }}>Contact</li>
            </LinkScroll>
          </ul>
        </div>
      </div>
    </div>
  );
};
export default Navbar;
