import React from "react";
import { Link } from "react-router-dom";
import community from "../../../media/community.png";

const Home = (props) => {
  return (
    <div className="home">
      <div className="home__left">
        <p>
          Lorem Ipsum is simply a dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy text ever
          since the 1500s.
        </p>
        <div className="home__left__ButtonContent">
          <Link to="/loginRegister">
            <div className="button">
              {props.id === "" ? "Get Started" : "My Profile"}
            </div>
          </Link>
        </div>
      </div>
      <div className="home__right">
        <img src={community} height="300px" alt="community_image" />
      </div>
    </div>
  );
};
export default Home;
