import React, { useState, useEffect } from "react";
import DropDown from "../../utils/DropDown/Dropdown";
import { Icon } from "semantic-ui-react";
import Modal from "../../utils/Modal/Modal";
import TechDetailsModal from "../../utils/Modal/TechDetailsModal";

import axios from "axios";

const Community = (props) => {
  const [downArrow, setDownArrow] = useState(true);
  const [login, setLogin] = useState({ loggedIn: false });
  const [selectValues, setSelectValues] = useState([]);
  const enableModal = () => {
    return (
      <div className="reviews__addNewReview">
        <Icon name="add" size="large" color="red" />
        <p>Add your community link</p>
      </div>
    );
  };
  const enableModalCommunityDetails = (name) => {
    return <div className="community__contents__boxes">{name}</div>;
  };
  const Technology_Options = [
    {
      key: "Web Development",
      text: "Web Development",
      value: "Web Development",
    },
    {
      key: "Android Development",
      text: "Android Development",
      value: "Android Development",
    },
    {
      key: "AR-VR",
      text: "AR-VR",
      value: "AR-VR",
    },
    {
      key: "App Development",
      text: "App Development",
      value: "App Development",
    },
    {
      key: "Blockchain Development",
      text: "Blockchain Development",
      value: "Blockchain Development",
    },
    {
      key: "Machine Learning",
      text: "Machine Learning",
      value: "Machine Learning",
    },
  ];
  const maxValue =
    window.innerWidth > 900 ? 10 : window.innerWidth < 500 ? 6 : 8;
  const onChangeSelectValue = (e, { value }) => {
    setSelectValues(value);
  };
  const availableInSelectValue = (value) => {
    if (selectValues.length === 0) {
      return true;
    }
    // selectValues.map((selectValue) => {
    //   if (value === selectValue) {
    //     return true;
    //   }
    // });
    for (let i = 0; i < selectValues.length; i++) {
      if (selectValues[i] === value) {
        return true;
      }
    }
    return false;
  };
  const [communityDetails, setCommunityDetails] = useState([]);
  useEffect(() => {
    setLogin(props.id === "" ? false : true);
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/general/19012001/groups`)
      .then((res) => {
        console.log(res);
        setCommunityDetails(res.data.data);
      })
      .catch((err) => console.log(err));
  }, []);
  const addingNewCommunity = async (community) => {
    axios
      .patch(
        `${process.env.REACT_APP_BACKEND_URL}/users/${props.id}/makeNewGroup`,
        community
      )
      .then((res) => {
        if (res.data.validity)
          axios
            .post(
              `${process.env.REACT_APP_BACKEND_URL}/general/addingGroup`,
              community
            )
            .then((res) => {
              if (!res.data.validity) {
                axios
                  .patch(
                    `${process.env.REACT_APP_BACKEND_URL}/general/addingGroup`,
                    community
                  )
                  .then((res) => console.log(res))
                  .catch((err) => console.log(err));
              }
            });
      })
      .catch((err) => console.log(err));
    setCommunityDetails([...communityDetails, community]);
  };
  return (
    <div className="community">
      <p className="community__top">community</p>
      {login.loggedIn ? (
        <Modal
          trigger={enableModal()}
          type="community"
          revertingFCN={addingNewCommunity}
          options={Technology_Options}
        />
      ) : (
        ""
      )}
      <div className="community__searchbar">
        <DropDown
          onChange={onChangeSelectValue}
          values={selectValues}
          options={Technology_Options}
        />
      </div>
      <div className="community__contents">
        {communityDetails.map((community, index) => {
          return index < maxValue && availableInSelectValue(community.about) ? (
            <TechDetailsModal
              trigger={enableModalCommunityDetails(community.technology_Name)}
              details={community}
            />
          ) : (
            !downArrow && availableInSelectValue(community.about) && (
              <TechDetailsModal
                trigger={enableModalCommunityDetails(community.technology_Name)}
                details={community}
              />
            )
          );
        })}
      </div>
      {communityDetails.length > maxValue ? (
        <div className="community__showMore">
          <div
            className="community__text"
            onClick={() => setDownArrow(!downArrow)}
          >
            {downArrow ? <p>Show more</p> : <p>Show Less</p>}
            {downArrow ? (
              <Icon name="angle down" color="black" size="large" />
            ) : (
              <Icon name="angle up" color="black" size="large" />
            )}
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};
export default Community;
