import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { Icon } from "semantic-ui-react";

const LoginRegister = () => {
  const [login, setLogin] = useState(false);
  const [loginDetails, setLoginDetails] = useState({ email: "", password: "" });
  const [registerDetails, setRegisterDetails] = useState({
    name: "",
    email: "",
    password: "",
  });
  useEffect(() => {
    document.title = "Authentication";
  }, []);
  const [loggedInOrRegister, setLoggedInOrRegister] = useState(false);

  const loginRegister = (e, type) => {
    e.preventDefault();
    if (type === "register") {
      axios
        .post(
          `${process.env.REACT_APP_BACKEND_URL}/users/register`,
          registerDetails
        )
        .then((res) => {
          setLoggedInOrRegister(res.data.validity);
          setRegisterDetails({ name: "", email: "", password: "" });
        })
        .catch((err) => console.log(err));
    } else if (type === "login") {
      axios
        .post(`${process.env.REACT_APP_BACKEND_URL}/users/login`, loginDetails)
        .then((res) => {
          setLoggedInOrRegister(res.data.validity);
          setLoginDetails({ email: "", password: "" });
        })
        .catch((err) => console.log(err));
    }
  };
  const onChangeInputs = (e, type) => {
    if (type === "login") {
      const { name, value } = e.target;
      setLoginDetails((prevLoginDetails) => {
        return {
          ...prevLoginDetails,
          [name]: value,
        };
      });
    } else if (type === "register") {
      const { name, value } = e.target;
      setRegisterDetails((prevRegisterDetails) => {
        return {
          ...prevRegisterDetails,
          [name]: value,
        };
      });
    }
  };
  if (loggedInOrRegister) {
    window.location.href = "/";
  } else {
    return (
      <div className="loginRegister">
        <Link to="/">
          <div
            className="ml-5 mb-3 loginRegisterBackToHome"
            style={{
              cursor: "pointer",
              fontFamily: "monospace",
              fontSize: "1.9rem",
              fontWeight: "900",
              color: "white",
              wordSpacing: "-10px",
              display: "flex",
              justifyContent: "flex-start",
              alignItems: "center",
            }}
          >
            <Icon
              style={{ marginTop: "5px", marginRight: "20px" }}
              name="arrow left"
            />
            <p>BACK TO HOME</p>
          </div>
        </Link>
        <div className="loginRegister__block">
          <div className="loginRegister__block__buttons">
            <p
              onClick={() => setLogin(true)}
              style={{ background: !login ? "#00000025" : "" }}
            >
              Login
            </p>
            <p
              onClick={() => setLogin(false)}
              style={{ background: !login ? "" : "#00000025" }}
            >
              Sign up
            </p>
          </div>
          <div className="loginRegister__block__content">
            {login ? (
              <div className="loginRegister__block__content__login">
                <p className="loginRegister__block__content__login__text">
                  Login
                </p>
                <form onSubmit={(e) => loginRegister(e, "login")}>
                  <input
                    type="text"
                    name="email"
                    placeholder="email"
                    onChange={(e) => onChangeInputs(e, "login")}
                    value={loginDetails.email}
                  />
                  <input
                    type="password"
                    name="password"
                    placeholder="password"
                    onChange={(e) => onChangeInputs(e, "login")}
                    value={loginDetails.password}
                  />
                  <button type="submit">Login</button>
                </form>
              </div>
            ) : (
              <div className="loginRegister__block__content__register">
                <p className="loginRegister__block__content__register__text">
                  Sign up
                </p>
                <form onSubmit={(e) => loginRegister(e, "register")}>
                  <input
                    type="text"
                    name="name"
                    placeholder="name"
                    onChange={(e) => onChangeInputs(e, "register")}
                    value={registerDetails.name}
                  />
                  <input
                    type="email"
                    name="email"
                    placeholder="email"
                    onChange={(e) => onChangeInputs(e, "register")}
                    value={registerDetails.email}
                  />
                  <input
                    type="password"
                    name="password"
                    placeholder="password"
                    onChange={(e) => onChangeInputs(e, "register")}
                    value={registerDetails.password}
                  />
                  <button type="submit">Sign up</button>
                </form>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
};
export default LoginRegister;
