import React, { useState } from "react";
import { Icon } from "semantic-ui-react";

const Contact = () => {
  const [downArrow, setDownArrow] = useState(true);
  return (
    <div className="contact">
      <div
        className="contact__content__contact"
        style={{
          display: window.innerWidth <= 900 && downArrow ? "none" : "",
        }}
      >
        <div className="contact__content__contact__heading">Contact Us</div>
        <div className="contact__content__contact__details">
          <div className="contact__content__contact__details__box">
            <Icon name=" address book outline" size="large" />
            <div className="contact__content__contact__details__box__name">
              <p>Tilak Nagar ND-18</p>
            </div>
          </div>
          <div className="contact__content__contact__details__box">
            <Icon name="mail" size="large" />
            <div className="contact__content__contact__details__box__name">
              <p>manjotsinghdhanjal@gmail.com</p>
            </div>
          </div>
          <div className="contact__content__contact__details__box">
            <Icon name="phone" size="large" />
            <div className="contact__content__contact__details__box__name">
              <p>9999999999</p>
            </div>
          </div>
          <div className="contact__content__contact__details__box">
            <Icon name="text telephone" size="large" />
            <div className="contact__content__contact__details__box__name">
              <p>011-44444444</p>
            </div>
          </div>
        </div>
        <div className="contact__content__contact__socialMedia">
          <Icon name="facebook" color="blue" size="large" />
          <Icon name="whatsapp" color="green" size="large" />
          <Icon name="linkedin" color="blue" size="large" />
          <Icon name="instagram" color="pink" size="large" />
          <Icon name="twitter" color="blue" size="large" />
        </div>
      </div>
      <div className="contact__content">
        <div className="contact__content__heading">
          <p>Get in Touch</p>
        </div>
        <div className="contact__content__description">
          <p>Feel free to drop us a line below!</p>
        </div>
        <div className="contact__content__content">
          <form
            onSubmit={(e) => {
              e.preventDefault();
            }}
          >
            <input type="text" name="name" placeholder="name" />
            <input type="text" name="email" placeholder="email" />
            <textarea
              type="text"
              name="message"
              placeholder="message"
              multiple
            />
            <button type="submit">Submit</button>
          </form>
        </div>
        <div
          style={{
            marginTop: "10px",
            display: window.innerWidth <= 900 ? "" : "none",
          }}
        >
          {downArrow ? (
            <Icon
              name="angle down"
              size="large"
              onClick={() => setDownArrow(false)}
            />
          ) : (
            <Icon
              name="angle up"
              size="large"
              onClick={() => setDownArrow(true)}
            />
          )}
        </div>
      </div>
    </div>
  );
};
export default Contact;
