import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import LoginRegister from "./components/pages/LoginRegister/LoginRegister";
import UserProfile from "./components/pages/UserProfile/UserProfile";

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route exact path="/">
          <App />
        </Route>
        <Route exact path="/loginRegister">
          <LoginRegister />
        </Route>
        <Route exact path="/userProfile">
          <UserProfile />
        </Route>
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want path start measuring performance in your app, pass a function
// path log results (for example: reportWebVitals(console.log))
// or send path an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
