import React, { useEffect, useState } from "react";
import ReviewBar from "../../utils/ReviewsBar/ReviewBar";
import { Icon } from "semantic-ui-react";
import Modal from "../../utils/Modal/Modal";

import axios from "axios";

const Reviews = (props) => {
  const [downArrow, setDownArrow] = useState(false);
  const [login, setLogin] = useState({ loggedIn: false });
  const [reviews, setReviews] = useState([]);
  const maxValue =
    window.innerWidth > 900 ? 10 : window.innerWidth < 500 ? 6 : 8;
  useEffect(() => {
    setLogin(props.id === "" ? false : true);
    axios
      .get(`${process.env.REACT_APP_BACKEND_URL}/general/19012001/reviews`)
      .then((res) => {
        // console.log(res);
        setReviews(res.data.data);
      })
      .catch((err) => console.log(err));
  }, []);
  const addingNewReview = (review) => {
    axios
      .patch(
        `${process.env.REACT_APP_BACKEND_URL}/users/${props.id}/addReview`,
        review
      )
      .then((res) => {
        // console.log(res);
        if (res.data.validity) {
          axios
            .post(
              `${process.env.REACT_APP_BACKEND_URL}/general/addingReview`,
              review
            )
            .then((res) => {
              // console.log(res);
              if (!res.data.validity) {
                axios
                  .patch(
                    `${process.env.REACT_APP_BACKEND_URL}/general/addingReview`,
                    review
                  )
                  .then((res) => console.log(res))
                  .catch((err) => console.log(err));
              }
            });
        }
      })
      .catch((err) => console.log(err));
    setReviews([...reviews, review]);
  };
  const enableModal = () => {
    return (
      <div className="reviews__addNewReview">
        <Icon name="add" size="large" color="red" />
        <p>Add your review</p>
      </div>
    );
  };
  return (
    <div className="reviews">
      <p className="reviews__top">reviews</p>
      {login.loggedIn ? (
        <Modal
          trigger={enableModal()}
          type={"reviews"}
          revertingFCN={addingNewReview}
        />
      ) : (
        ""
      )}
      <div className="reviews__content">
        {reviews.map((review, index) => {
          return index < maxValue ? (
            <ReviewBar key={review.key} data={review} />
          ) : (
            !downArrow && <ReviewBar key={review.key} data={review} />
          );
        })}
      </div>
      {reviews.length > maxValue ? (
        <div className="reviews__showMore">
          <div
            className="reviews__text"
            onClick={() => setDownArrow(!downArrow)}
          >
            {downArrow ? <p>Show more</p> : <p>Show Less</p>}
            {downArrow ? (
              <Icon name="angle down" color="black" size="large" />
            ) : (
              <Icon name="angle up" color="black" size="large" />
            )}
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
};
export default Reviews;
