import React, { useState, useEffect } from "react";
import { Icon } from "semantic-ui-react";
import Modal from "../../utils/Modal/Modal";
import axios from "axios";
import { Link } from "react-router-dom";

const UserProfile = () => {
  const [profile, setProfile] = useState({});
  useEffect(() => {
    document.title = "Profile";
    axios
      .get(
        `${process.env.REACT_APP_BACKEND_URL}/users/600f25170e55ad39a8559dc0`
      )
      .then((res) => {
        console.log(res);
        setProfile(res.data.message);
      })
      .catch((err) => console.log(err));
  }, []);
  const savingProfile = (profileDetails) => {
    axios
      .patch(
        `${process.env.REACT_APP_BACKEND_URL}/users/updateDetails/600c7c8cd12cf60d1cfe7ad7`,
        profileDetails
      )
      .then((res) => {
        console.log("res : ", res);
        if (res.data.validity) {
          profileDetails["email"] = profile.email;
          profileDetails["groupsCreated"] = profile.groupsCreated;
          profileDetails["reviewsMade"] = profile.reviewsMade;
          profileDetails["myJoinedGroups"] = profile.myJoinedGroups;
          console.log("profileDetails : ", profileDetails);
          setProfile(profileDetails);
        }
      });
  };
  const editIcon = () => {
    return (
      <Icon style={{ cursor: "pointer" }} name="edit" inverted size="big" />
    );
  };
  console.log("profile : ", profile);
  return (
    <div className="container mt-4">
      <div className="mr-0 ml-auto d-flex justify-content-end pb-3">
        <Modal
          trigger={editIcon()}
          type={"editProfile"}
          revertingFCN={savingProfile}
        />
      </div>
      <div className="main-body">
        <Link to="/">
          <div
            className="ml-5 mb-3 loginRegisterBackToHome"
            style={{
              cursor: "pointer",
              fontFamily: "monospace",
              fontSize: "1.9rem",
              fontWeight: "900",
              color: "white",
              wordSpacing: "-10px",
              display: "flex",
              justifyContent: "flex-start",
              alignItems: "center",
            }}
          >
            <Icon
              style={{ marginTop: "5px", marginRight: "20px" }}
              name="arrow left"
            />
            <p>BACK TO HOME</p>
          </div>
        </Link>
        <div className="row gutters-sm">
          <div className="col-md-4 mb-3">
            <div className="card">
              <div className="card-body">
                <div className="d-flex flex-column align-items-center text-center">
                  <div className="mt-3">
                    <h4>{profile.name}</h4>
                    <p className="text-secondary mb-1">{profile.bio}</p>
                    <p className="text-muted font-size-sm">{profile.Address}</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="card mt-3">
              <ul className="list-group list-group-flush">
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                  <h6 className="mb-0">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      stroke-width="2"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      className="feather feather-globe mr-2 icon-inline"
                    >
                      <circle cx="12" cy="12" r="10"></circle>
                      <line x1="2" y1="12" x2="22" y2="12"></line>
                      <path d="M12 2a15.3 15.3 0 0 1 4 10 15.3 15.3 0 0 1-4 10 15.3 15.3 0 0 1-4-10 15.3 15.3 0 0 1 4-10z"></path>
                    </svg>
                    Website
                  </h6>
                  <span className="text-secondary">{profile.Website}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                  <h6 className="mb-0">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      stroke-width="2"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      className="feather feather-github mr-2 icon-inline"
                    >
                      <path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path>
                    </svg>
                    Github
                  </h6>
                  <span className="text-secondary">{profile.Github}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                  <h6 className="mb-0">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      stroke-width="2"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      className="feather feather-twitter mr-2 icon-inline text-info"
                    >
                      <path d="M23 3a10.9 10.9 0 0 1-3.14 1.53 4.48 4.48 0 0 0-7.86 3v1A10.66 10.66 0 0 1 3 4s-4 9 5 13a11.64 11.64 0 0 1-7 2c9 5 20 0 20-11.5a4.5 4.5 0 0 0-.08-.83A7.72 7.72 0 0 0 23 3z"></path>
                    </svg>
                    Twitter
                  </h6>
                  <span className="text-secondary">{profile.Twitter}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                  <h6 className="mb-0">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      stroke-width="2"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      className="feather feather-instagram mr-2 icon-inline text-danger"
                    >
                      <rect
                        x="2"
                        y="2"
                        width="20"
                        height="20"
                        rx="5"
                        ry="5"
                      ></rect>
                      <path d="M16 11.37A4 4 0 1 1 12.63 8 4 4 0 0 1 16 11.37z"></path>
                      <line x1="17.5" y1="6.5" x2="17.51" y2="6.5"></line>
                    </svg>
                    Instagram
                  </h6>
                  <span className="text-secondary">{profile.Instagram}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between align-items-center flex-wrap">
                  <h6 className="mb-0">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="24"
                      height="24"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      stroke-width="2"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      className="feather feather-facebook mr-2 icon-inline text-primary"
                    >
                      <path d="M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z"></path>
                    </svg>
                    Facebook
                  </h6>
                  <span className="text-secondary">{profile.Facebook}</span>
                </li>
              </ul>
            </div>
          </div>
          <div className="col-md-8">
            <div className="card mb-3">
              <div className="card-body">
                <div className="row">
                  <div className="col-sm-3">
                    <h6 className="mb-0">Full Name</h6>
                  </div>
                  <div className="col-sm-9 text-secondary">{profile.name}</div>
                </div>
                <hr />
                <div className="row">
                  <div className="col-sm-3">
                    <h6 className="mb-0">Email</h6>
                  </div>
                  <div className="col-sm-9 text-secondary">{profile.email}</div>
                </div>
                <hr />
                <div className="row">
                  <div className="col-sm-3">
                    <h6 className="mb-0">Phone</h6>
                  </div>
                  <div className="col-sm-9 text-secondary">
                    {profile.phoneNumber}
                  </div>
                </div>
                <hr />
                <div className="row">
                  <div className="col-sm-3">
                    <h6 className="mb-0">Mobile</h6>
                  </div>
                  <div className="col-sm-9 text-secondary">
                    {profile.mobileNumber}
                  </div>
                </div>
                <hr />
                <div className="row">
                  <div className="col-sm-3">
                    <h6 className="mb-0">Address</h6>
                  </div>
                  <div className="col-sm-9 text-secondary">
                    {profile.Address}
                  </div>
                </div>
              </div>
            </div>
            <div className="card mb-3">
              <div className="card h-100">
                <div className="card-body">
                  <h6
                    className="d-flex align-items-center mb-3"
                    style={{ fontWeight: "900" }}
                  >
                    Groups Created
                  </h6>
                  {profile.groupsCreated &&
                    profile.groupsCreated.map((group) => {
                      return (
                        <div
                          style={{
                            display: "flex",
                            justifyContent: "space-between",
                            flexDirection: "row",
                            marginTop: "20px",
                          }}
                        >
                          <p
                            style={{
                              width: "40%",
                              wordBreak: "break-all",
                              marginBottom: "0px",
                              display: "flex",
                              justifyContent: "flex-start",
                              alignItems: "center",
                              marginRight: "5px",
                            }}
                          >
                            {group.technology_Name}
                          </p>
                          <small
                            color="blue"
                            style={{ width: "100%", wordBreak: "break-all" }}
                          >
                            {group.technology_link}
                          </small>
                        </div>
                      );
                    })}
                </div>
              </div>
            </div>
            <div className="card mb-3">
              <div className="card h-100">
                <div className="card-body">
                  <h6
                    className="d-flex align-items-center mb-3"
                    style={{ fontWeight: "900" }}
                  >
                    Reviews
                  </h6>
                  {profile.reviewsMade &&
                    profile.reviewsMade.map((reviews) => {
                      return (
                        <small
                          color="blue"
                          style={{
                            width: "100%",
                            wordBreak: "break-all",
                            marginTop: "20px",
                          }}
                        >
                          {reviews.review}
                        </small>
                      );
                    })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default UserProfile;
