import React, { useState } from "react";
import "./ReviewBar.css";
import { Rating } from "semantic-ui-react";

const ReviewBar = (props) => {
  const [liked, setLiked] = useState(false);
  return (
    <div
      className="reviewBar"
      onDoubleClick={() => setLiked(!liked)}
      style={{ background: liked ? "#ff000030" : "" }}
    >
      <div className="reviewsBar__content">
        <div className="reviewsBar__content__name">
          <p style={{ color: liked ? "#fff" : "" }}>{props.data.name}</p>
          <div className="reviewsBar__content__name__reviews">
            <Rating
              maxRating={5}
              defaultRating={props.data.rating}
              disabled
              icon="heart"
              size="large"
            />
            <div style={{ color: liked ? "#ffffff" : "", cursor: "pointer" }}>
              {/* (upvotes : {props.data.upvotes}) */}
            </div>
          </div>
        </div>
        <p className="reviewsBar__content__review">
          <p style={{ color: liked ? "#ffffff" : "" }}>{props.data.review}</p>
        </p>
      </div>
    </div>
  );
};
export default ReviewBar;
